* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
}

.App {
  display: flex;
  width: 100%;
}

.App .display {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bmi {
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bmi .container {
  margin: 10px;
  box-shadow: 0px 0px 12px #ccc;
  border-radius: 8px;
  padding: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-width: 300px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
.bmi .container .som {
  width: 100%;
}

.bmi .container .som button {
  width: 50%;
  background-color: #ffffff;
  color: #0a8ff5;
  font-size: 18px;
  font-weight: 500;
  border: 1px solid #0a8ff5;
  padding: 10px;
  margin: 10px auto;
}
.bmi .container .som button.active {
  background-color: #0a8ff5;
  color: white;
}
.bmi .container form {
  margin: 5px;
  padding: 5px auto;
}

.bmi .container .inputBar {
  width: 100%;
  font-size: 1.2rem;
  margin: 8px 0;
  border: 2px solid black;
  border-radius: 4px;
}
.bmi .container .inputBar input {
  width: 80%;
  font-size: 1.2rem;
  padding: 8px 4px;
  border: none;
  border-right: 1px solid black;
}

.bmi .container .inputBar span {
  width: 20%;
  color: #0a8ff5;
  padding: 10px;
  margin: auto;
}
.bmi .container .action {
  padding: 10px 0px;
}

.bmi .container .action button {
  display: block;
  width: 100%;
  font-size: 1.2rem;
  margin: 8px 0;
  padding: 10px 0;
  background-color: #0a8ff5;
  color: #fff;
  border: 1px solid #0a8ff5;
  border-radius: 8px;
  cursor: pointer;
}

.bmi .container .action button:nth-of-type(2) {
  background-color: #fff;
  color: #0a8ff5;
}

.bmi .messageBox {
  margin: 30px;
  min-width: 300px;
  min-height: 00px;
  box-shadow: 0px 0px 12px #ccc;
  border-radius: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.bmi .messageBox h2 {
  border-radius: 10px 10px 0px 0px;
  font-size: 22px;
  text-align: center;
  margin: 0;
  padding: 20px;
  background-color: #0a8ff5;
  color: white;
  width: 100%;
}
.bmi .messageBox div {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.bmi .messageBox div div {
  margin: 10px 0;
  display: flex;
  flex-direction: column;
  border-right: 2px solid grey;
}
.bmi .messageBox div .bmiResult {
  color: #0a8ff5;
  padding: 30px;
  font-size: 40px;
}
.bmi .messageBox h3 {
  align-self: flex-start;
  margin: 25px;
}
.bmi .messageBox .errorMessage {
  background-color: rgb(255, 217, 217);
  color: red;
}

.bmi .messageBox.error {
  border: 2px solid red;
  border-radius: 0px;
}
.bmi .messageBox p {
  margin: 25px;
  margin-top: 10px;
}

.img-container {
  text-align: center;
}

.img-container img {
  height: 200px;
}
@media (max-width: 749px) {
  .bmi .messageBox div {
    flex-direction: column-reverse;
  }
  .bmi .messageBox div div {
    margin: 10px;
    border-right: none;
    border-top: 2px solid grey;
  }
  .bmi .messageBox div .bmiResult {
    padding: 5px;
    margin: 5px;
  }
}

.navBar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  min-width: 200px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: linear-gradient(#001524, #011c2ffe);
  background-position: -30px;
  height: 100vh;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.navBar .header .navIcon {
  position: absolute;
  display: none;
  top: 28px;
  left: 10px;
}

.navBar h1 {
  margin: 3px;
  padding: 20px;
  text-align: center;
  color: white;
}

.navBar h1 span {
  color: #ff7d00;
}

.navBar .tools {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;
  padding: 10px;
}

.navBar .tools button {
  background-color: inherit;
  border: none;
  color: white;
  text-align: left;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  margin: 5px 3px;
  font-size: 16px;
  font-weight: 600;
}
.navBar button.active {
  background-color: #313e47c2;
}

.navBar .tools button:hover {
  background-color: #5f7687c2;
}
.navBar .navIcon {
  color: white;
  font-size: 30px;
}
@media (max-width: 669px) {
  .navBar .tools {
    display: none;
  }
  .App {
    flex-direction: column;
  }
  .navBar {
    height: 90px;
    width: 100%;
    margin: 0;
    position: relative;
  }
  .navBar .header .navIcon {
    display: flex;
  }
}

.navBar .tools.openBar {
  display: flex;
  background-color: #001524;
}

.calculator {
  background: linear-gradient(to right, #9c9c9c, #4b4b4b);
  box-shadow: 0px 0px 12px #ccc;
  display: grid;
  margin: 2rem;
  grid-template-columns: repeat(4, 6rem);
  grid-template-rows: minmax(7rem, auto) repeat(5, 6rem);
  justify-content: center;
  border-radius: 10px;
}

@media (max-width: 379px) {
  .calculator {
    grid-template-columns: repeat(4, 5rem);
    grid-template-rows: minmax(7rem, auto) repeat(5, 5rem);
  }
}
@media (max-width: 289px) {
  .calculator {
    grid-template-columns: repeat(4, 4rem);
    grid-template-rows: minmax(7rem, auto) repeat(5, 4rem);
  }
}
.calculator > button {
  font-size: 2rem;
  background-color: rgba(255, 255, 255, 0.6);
  border: 0.5px solid white;
}

.calculator > button:hover {
  background-color: rgba(224, 240, 255, 0.95);
  color: black;
}

.calculator .span-two {
  grid-column: span 2;
}

.calculator .hightlight1 {
  background-color: rgb(53, 145, 243);
}

.calculator .highlight2 {
  background-color: #ff7d00;
  color: white;
}
.calculator .output {
  grid-column: 1 / -1;
  background-color: none;
  padding: 7px;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  flex-direction: column;
  word-wrap: break-word;
  word-break: break-all;
}

.calculator .pre-operand {
  color: rgba(242, 242, 242, 0.92);
  font-size: 1.5rem;
}
.calculator .current-operand {
  color: white;
  font-size: 2rem;
}

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.counter span {
  font-size: 160px;
  padding: 15px;
  margin: 15px;
  color: #2b2b2c;
}

.counter button.actionBtn {
  margin: 20px 50px;
  font-size: 40px;
  height: 80px;
  width: 80px;
  border-radius: 50px;
  background-color: #ff7d00;
  border: 2px solid #ff7d00;
  color: white;
}

.counter button.actionBtn:nth-of-type(2) {
  color: #ff7d00;
  background-color: white;
}
.counter .changeBtn {
  background-color: #0a8ff5;
  margin: 20px 15px;
  border-radius: 5px;
  border: 2px solid #0a8ff5;
  display: inline-flex;
}
.counter .changeBtn button {
  font-size: 25px;
  width: 120px;
  background-color: #0a8ff5;
  border: none;
  color: white;
  margin: 0 10px;
}

.counter .changeBtn input {
  border: none;
  font-size: 20px;
  width: 80px;
  color: #0a8ff5;
  text-align: center;
}
.counter .errorMessage {
  color: red;
}

.counter .error {
  background-color: rgb(255, 217, 217);
  border: 2px solid red;
  border-radius: 0px;
  margin: 10px;
  padding: 20px;
  box-shadow: 0px 0px 12px #ccc;
}

.counter .reset {
  border: 2px solid red;
  background-color: red;
  font-size: 27px;
  color: white;
  margin: 20px;
  padding: 10px 40px;
  border-radius: 10px;
}

.counter .reset:hover {
  color: red;
  background-color: white;
}

@media (max-width: 509px) {
  .counter .btnContainer {
    display: flex;
  }
  .counter .btnContainer .changeBtn {
    flex-direction: column;
    align-items: center;
  }
  .counter .btnContainer .changeBtn input {
    width: 100%;
    height: 30px;
  }
}

.joke {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.joke .container {
  width: 90%;
  margin: 20px 10px;
  padding: 15px;
  border: 2px solid rgb(69, 69, 69);
  border-radius: 10px;
}

.joke .container div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.joke .container div h2 {
  font-size: 30px;
}
.joke .container div .jokeBtn {
  padding: 10px;
  font-size: 20px;
  color: white;
  background-color: #0a8ff5;
  border: 2px solid #0a8ff5;
  border-radius: 5px;
}

.joke .display {
  border: 1px solid gray;
  color: rgb(38, 38, 38);
  margin: 20px 0;
  padding: 15px;
  min-height: 90px;
  border-radius: 10px;
}

.joke .container .options {
  flex-direction: column;
  align-items: flex-start;
}

.joke .container .options fieldset {
  margin: 10px 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border-radius: 10px;
  border: 1px soild gray;
  flex-wrap: wrap;
}
.joke .container .options fieldset legend {
  font-size: 18px;
  font-weight: 500px;
  margin: auto 10px;
  padding: auto 10px;
}
.joke .container .options fieldset div {
  margin: 5px;
}

.joke .container .options fieldset div label {
  margin: auto 10px;
}

.currencyConverter .exBlock {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (max-width: 824px) {
  .currencyConverter .exBlock {
    flex-direction: column;
  }
}
.currencyConverter .exBlock .exInput {
  margin: 30px;
  background-color: #0a8ff5;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
.currencyConverter .exBlock .exInput input {
  text-align: center;
  font-size: 25px;

  width: 70%;
  border: none;
  background-color: #0a8ff5;
  border-radius: 15px 0 0 15px;
  border-right: 1px solid black;
  color: white;
  padding: 15px 0;
}
.currencyConverter .exBlock .exInput select {
  text-align: center;
  font-size: 25px;
  width: 30%;
  border: none;
  padding: 15px 0;
  color: white;
  height: 100%;
  border-radius: 0 15px 15px 0;
  background-color: #0a8ff5;
}

.currencyConverter .exBlock .exIcon {
  font-size: 30px;
  color: rgb(91, 91, 91);
}

